import React, { Component } from 'react'
import '../Coindash/Coindash.css'

class Coindash extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container-fluid" style={{ background: '#2e3880' }}>
          <div className="row">
            <div className="col-xxl-6 col-xl-6 col-lg-6 col-md-12 col-sm-12 col-xs-12">
              <img src="/check.jpg" className="responsive" />
            </div>
            <div className="col-xxl-6 col-xl-6 col-lg-6 col-md-12 col-sm-12 col-xs-12">
              <div className="img-sec-part">
                <p className="img-text-p">WHY AROW</p>
                <br />
                <p className="img-text-p">
                  Together, We’re Modernizing Global Payments
                </p>
                <br />
                <p className="img-text-p">
                  Our mission is to help investors save time researching crypto
                  assets, avoid dead projects or scams, and find crypto unicorns
                  that have the potential to disrupt entire industries.
                </p>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Coindash
