import React, { Component } from 'react'
import '../Footer/Footer.css'

class Footer extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <div
            className="row"
            style={{ textAlign: 'center', background: 'black', color: 'white' }}
          >
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div className="footer-heading">
                <p style={{ margin: '3rem', fontSize: '2rem' }} className="home_text">
                  Subscribe for Our Newsletter
                </p>
              </div>
            </div>
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <input
                type="text"
                placeholder="Enter your e-mail address"
                className="footer-input subscribe"
                name="search"
              />
            </div>
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div className="footer-navigation">
                <ul className="footer__social">
                  <li>
                    <a class="fa fa-linkedin social-bar"></a>
                  </li>
                  <li>
                    <a class="fa fa-facebook social-bar"></a>
                  </li>
                  <li>
                    <a class="fa fa-instagram social-bar"></a>
                  </li>
                  <li>
                    <a class="fa fa-twitter social-bar"></a>
                  </li>
                  <li>
                    <a class="fa fa-skype social-bar"></a>
                  </li>
                </ul>
              </div>
            </div>
            <div className=" col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <p>© 2020 AROW (DeFi)</p>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Footer
