import React, { Component } from 'react'
import '../Offers/Offers.css'

class Offers extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container" id="offer">
          <div className="row">
            <div
              className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12"
              style={{ marginTop: '3rem' }}
            >
              <h2
                className="home_text section__text"
                style={{ fontSize: '2rem' }}
              >
                Our Offers
              </h2>
            </div>
          </div>

          <div className="row" style={{ margin: '1rem' }}>
            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12 offer-col offer-col-col">
              <div class="card offer-body">
                <div class="card-body offers-class">
                  <p className="offerbody-p">⚡️Basic</p>
                  <p>Buy AROW </p>
                  <p>Worth of $10 - $1000</p>
                  <p>Price 101$</p>
                  <p>Get Discount 5%</p>
                  <button
                    type="button"
                    class="offers__btn"
                    style={{ cursor: 'pointer' }}
                  >
                    BUY
                  </button>
                </div>
              </div>
            </div>
            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12 offer-col offer-col-col">
              <div class="card offer-body">
                <div class="card-body offers-class">
                  <p className="offerbody-p">⚡️Professional </p>
                  <p>Buy AROW </p>
                  <p>Worth of $1001 - $1500</p>
                  <p>Price 101$</p>
                  <p>Get Discount 10%</p>
                  <button
                    type="button"
                    class="offers__btn"
                    style={{ cursor: 'pointer' }}
                  >
                    BUY
                  </button>
                </div>
              </div>
            </div>
            <div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12 offer-col offer-col-col">
              <div class="card offer-body">
                <div class="card-body offers-class">
                  <p className="offerbody-p">⚡️Premium </p>
                  <p>Buy AROW </p>
                  <p>Worth $1501 or more than</p>
                  <p>Price 101$</p>
                  <p>Get Discount 15%</p>
                  <button
                    type="button"
                    class="offers__btn"
                    style={{ cursor: 'pointer' }}
                  >
                    BUY
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Offers
