import React, { Component } from 'react'
import '../Roadmap/Roadmap.css'

class Roadmap extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container" id="roadmap">
          <div className="row">
            <div className="col-12">
              <h2 className="home_text" style={{ marginTop: '2rem' }}>
                Our Roadmap
              </h2>
              <span className="section__tagline" style={{margin:'0rem',padding:'2rem'}}>
                With help from our teams, contributors and investors these are
                the milestones we have achieved so far and milestones that we
                are looking forward to achieve
              </span>
            </div>
            <div className="col-12 col-md-10 offset-md-1 col-lg-6 offset-lg-3 col-xl-12 offset-xl-0">
              <ul className="roadmap">
                <li className="active">
                  <span className="roadmap-span">November 2020</span>
                  <p className="roadmap-p">ArowEx Exchange Development</p>
                </li>
                <li className="active">
                  <span className="roadmap-span">December 2020</span>
                  <p className="roadmap-p">Private pre-sale and Public sale</p>
                </li>
                <li>
                  <span className="roadmap-span">January 2021</span>
                  <p className="roadmap-p">Listing on ArowEx Exchange</p>
                </li>
                <li>
                  <span className="roadmap-span">February 2021</span>
                  <p className="roadmap-p">
                    Stacking program will be start on ArowEx Exchange
                  </p>
                </li>
                <li>
                  <span className="roadmap-span">March 2021</span>
                  <p className="roadmap-p">
                    Development of the ArowEx Android and iOS application
                  </p>
                </li>
                <li>
                  <span className="roadmap-span">April 2021</span>
                  <p className="roadmap-p">
                    ArowEx Token trading will be start on top most exchanges
                  </p>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <button
              type="button"
              class="btn btn-primary signbtn"
              style={{ marginBottom: '3rem' }}
            >
              PURCHASE NOW
            </button>
          </div>

          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <p className="roadmap-footer">
              *This Roadmap may change in terms of priorities and timeline based
              on changing market conditions business opportunities and threats.
            </p>
          </div>
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <p className="roadmap-footer">
            Our success story, to be continued .....
            </p>
          </div>
        </div>
      </>
    )
  }
}

export default Roadmap
