import React, { Component } from 'react'
import { CanvasJSChart } from 'canvasjs-react-charts'

class Graph1 extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    const options = {
      animationEnabled: true,
      exportEnabled: true,
      backgroundColor: '#20284d',
      title: {
        text: 'Soft-cap Budget Allocation',
        fontColor: '#20284d',
      },
      data: [
        {
          type: 'pie',
          indexLabel: '{label}: {y}%',
          startAngle: -90,
          dataPoints: [
            { y: 30, label: 'Product Development', indexLabelFontColor: 'white' },
            { y: 35, label: 'Marketing, sales & partnerships', indexLabelFontColor: 'white' },
            { y: 10, label: 'Operations', indexLabelFontColor: 'white' },
            { y: 10, label: 'Legal', indexLabelFontColor: 'white' },
            { y: 15, label: 'Management', indexLabelFontColor: 'white' }
          ],
        },
      ],
    }
    return (
      <>
        <CanvasJSChart options={options} />
      </>
    )
  }
}

export default Graph1
