import React, { Component } from 'react'
import '../Navbar/Navbar.css'
import '../Carousel/Carousel.css'

import $ from 'jquery'
import Tagline from '../Tagline/Tagline'

class Navbar extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentDidMount() {
    console.log('width :::::', window.innerWidth)

    $(function () {
      $(document).scroll(function () {
        var $nav = $('.navbar-fixed-top')
        $nav.toggleClass('scrolled', $(this).scrollTop() > $nav.height())
      })
    })

    $('#start_button').click(function () {
      $('html, body').animate(
        {
          scrollTop: $('#started').offset().top,
        },
        1000,
      )
      if (window.innerWidth < 770) {
        const sidebar = document.querySelector('.top-navbar')
        sidebar.remove('open')
      }
    })

    $('#start_button2').click(function () {
      $('html, body').animate(
        {
          scrollTop: $('#offer').offset().top,
        },
        1000,
      )
      if (window.innerWidth < 770) {
        const sidebar = document.querySelector('.top-navbar')
        sidebar.remove('open')
      }
    })

    $('#start_button3').click(function () {
      $('html, body').animate(
        {
          scrollTop: $('#roadmap').offset().top,
        },
        1000,
      )
      if (window.innerWidth < 770) {
        const sidebar = document.querySelector('.top-navbar')
        sidebar.remove('open')
      }
    })

    $('#start_button4').click(function () {
      $('html, body').animate(
        {
          scrollTop: $('#contact').offset().top,
        },
        1000,
      )
      if (window.innerWidth < 770) {
        const sidebar = document.querySelector('.top-navbar')
        sidebar.remove('open')
      }
    })
  }
  render() {
    return (
      <>
        <div className="container">
          <div className="logo-view">
          <img
                    src="./main-logo.png"
                    style={{ height: '3rem', width: '3rem' }}
                  />

          </div>
          <div className="hamburger">
            <div className="line line1"></div>
            <div className="line line2"></div>
            <div className="line line3"></div>
          </div>
          <div className="top-navbar">
            <div
              id="navigation"
              className="navbar navbar-dark bg-primary navbar-fixed-top"
            >
              <div className="row">
                <div className="col-xl-2 col-lg-2 col-md-1 col-sm-12 col-xs-12">
                  <img
                    src="./main-logo.png"
                    style={{ height: '3rem', width: '3rem' }}
                  />
                </div>
                <div
                  className="col-xl-8 col-lg-8 col-md-10 col-sm-12 col-xs-12"
                  style={{
                    display: 'flex',
                    justifyContent: 'space-around',
                    fontSize: 'larger',
                  }}
                >
                  <ul className="nav navbar-nav">
                    <li className="nav-item">
                      <a id="start_button" href="#started" className="nav-link">
                        Home
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#projects"
                        className="nav-link"
                        id="start_button2"
                      >
                        Offers
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#roadmap"
                        className="nav-link"
                        id="start_button3"
                      >
                        Roadmap
                      </a>
                    </li>
                    <li className="nav-item">
                      <a href="#blog" className="nav-link">
                        Blog
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#contact-us"
                        className="nav-link"
                        id="start_button4"
                      >
                        Contacts
                      </a>
                    </li>
                  </ul>
                </div>
                <div className="col-xl-2 col-lg-2 col-md-1 col-sm-12 col-xs-12">
                  <div className="navbar-btn-signin">
                    <button
                      type="button"
                      class="btn btn-primary navbar-btn"
                      style={{ float: 'right', fontSize: '14px' }}
                    >
                      SIGN IN
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Navbar
