import React, { Component } from 'react'
import { CanvasJSChart } from 'canvasjs-react-charts'

class Graph2 extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    const options = {
      animationEnabled: true,
      exportEnabled: true,
      backgroundColor: '#20284d',
      title: {
        text: 'Soft-cap Budget Allocation',
        fontColor: '#20284d',
      },
      data: [
        {
          type: 'pie',
          indexLabel: '{label}: {y}%',
          startAngle: -90,
          dataPoints: [
            {
              y: 70,
              label: 'Distributed to Community',
              indexLabelFontColor: 'white',
            },
            {
              y: 15,
              label: 'Reserved Funding',
              indexLabelFontColor: 'white',
            },
            { y: 10, label: 'Founders and Team', indexLabelFontColor: 'white' },
            { y: 4, label: 'Advisors', indexLabelFontColor: 'white' },
            { y: 1, label: 'Airdrop', indexLabelFontColor: 'white' },
          ],
        },
      ],
    }
    return (
      <>
        <CanvasJSChart options={options} />
      </>
    )
  }
}

export default Graph2
