import React, { Component } from 'react'
import '../Contact/Contact.css'

class Contact extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container" id="contact">
          <div className="row" style={{ textAlign: 'center', padding: '3rem' }}>
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <p className="home_text" style={{fontSize:'2rem'}}>Get in Touch</p>
            </div>
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <p className="contact-text">
                We are always open and we welcome and questions you have for our
                team. If you wish to get in touch, please fill out the form
                below.
              </p>
            </div>
            <div className="col-xl-6 col-xxl-6 col-lg-6 col-md-12 col-sm-12 col-xs-12" style={{marginTop:'3rem'}}>
              <div className="contacts" style={{ display: 'block' }}>
                <ul className="contacts__list">
                  <li>
                    <span className="social-logo-color">
                      <i class="fa fa-map-marker" aria-hidden="true"></i>
                    </span>
                    <p>
                      The AROW (DeFi) Exchange
                    </p>
                  </li>
                  <li>
                    <span className="social-logo-color">
                      <i class="fa fa-clock-o" aria-hidden="true"></i>
                    </span>
                    <p>
                      Mon - Fri
                    </p>
                  </li>
                  <li>
                    <span className="social-logo-color">
                      <i class="fa fa-envelope-o" aria-hidden="true"></i>
                    </span>
                    <p>token@arowex.com</p>
                  </li>
                  {/* <li>
                    <span className="social-logo-color">
                      <i class="fa fa-mobile" aria-hidden="true"></i>
                    </span>
                    <p>+1 800 234-56-78</p>
                  </li> */}
                </ul>
              </div>
            </div>
            <div className="col-xl-6 col-xxl-6 col-lg-6 col-md-12 col-sm-12 col-xs-12">
              <form action="" className="form form--contacts">
                <input type="text" className="form__input subscribe" placeholder="Name" />
                <input
                  type="text"
                  className="form__input subscribe"
                  placeholder="Email"
                />
                <textarea
                  className="form__textarea subscribe"
                  placeholder="Message"
                ></textarea>
                <button className="form__btn" type="button">
                  <span>SEND</span>
                </button>
              </form>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Contact
