import React, { Component } from 'react'

class Carousel extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        <div className="container-fluid header-row">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div className="header-image">
                {/* <img src="../../Images/slide.jpg" alt="" srcset=""/> */}
                <div className="home_content">
                  <h1 className="home_text">AROW</h1>
                  <h1 className="home_text">(DeFi)</h1>
                </div>
                <div className="progress">
                  <div className="progress-bar"></div>
                  <div className="progress__line" style={{ left: '18%' }}>
                    <p>20%</p>
                  </div>
                  <div className="progress__line" style={{ left: '98%' }}>
                    <p>21k</p>
                  </div>
                </div>
                <div className="home_btns">
                  <button type="button" class="btn btn-primary navbar-btn">
                    Whitepaper
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Carousel
