import React, { Component } from 'react'
import $ from 'jquery'
import '../Tagline/Tagline.css'

class Tagline extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    return (
      <>
        <div className="container">
        <div
          id="started"
          className="tagline"
          style={{ padding: '3rem', background: '#20284d' }}
        >
          <div className="row">
            <h1
              style={{ textAlign: 'center', color: 'white' }}
              className="home_text section__text"
              style={{fontSize:'2rem'}}
            >
              How to Get Started
            </h1>
            <div
              className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12"
              style={{ marginTop: '2rem' }}
            >
              <div className="card border-box">
                <div className="card-body">
                  <h5 className="card-title tagline-number">01</h5>
                  <h6 className="card-subtitle mb-2 text-muted">
                    REGISTER ON THE SITE
                  </h6>
                  <p
                    className="card-text box4__text"
                    style={{ color: 'white' }}
                  >
                    Once your registration is accepted, you will automatically
                    become the newest member of the Arow (DeFi) Era. And you will get to
                    claim our proprietary Arow trading software for free.
                  </p>
                </div>
              </div>
            </div>

            <div
              className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12"
              style={{ marginTop: '2rem' }}
            >
              <div className="card border-box">
                <div className="card-body">
                  <h5 className="card-title tagline-number">02</h5>
                  <h6 className="card-subtitle mb-2 text-muted">
                    FUND YOUR ACCOUNT
                  </h6>
                  <p
                    className="card-text box4__text"
                    style={{ color: 'white' }}
                  >
                    Like any business, you need working capital to get
                    started. So to start profiting with the Arow (DeFi) Era, you must
                    invest any amount you desire of $101 or more.
                  </p>
                </div>
              </div>
            </div>

            <div
              className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12"
              style={{ marginTop: '2rem' }}
            >
              <div className="card border-box">
                <div className="card-body">
                  <h5 className="card-title tagline-number">03</h5>
                  <h6 className="card-subtitle mb-2 text-muted">FINISH</h6>
                  <p
                    className="card-text box4__text"
                    style={{ color: 'white' }}
                  >
                    Click trade to enjoy precise and accurate hands-free trading
                    powered by our award-winning algorithm. You can also set the
                    trading to manual if you prefer to trade on your own.
                  </p>
                </div>
              </div>
            </div>
            <div
              className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12"
              style={{ textAlign: 'center', marginTop: '2rem' }}
            >
              <button type="button" class="btn btn-primary signbtn">
                SIGN UP
              </button>
            </div>
          </div>
        </div>
        </div>
      </>
    )
  }
}

export default Tagline
